.fixed {
  position: fixed;
  top: 65%;
  left: 50%;
  transform: translate(-50%,-50%);
}
.pageTitle{
  text-align:center;
  font-size: 3em;
  margin-top:11vh;
}
.descTxt{
  height: 150px;
  overflow-y: scroll;
}
.vidContainer{
  background: orangered;
}
.score-bar{
  width:100%;
  position:fixed;
  left:0;
  bottom:0;
  height:20px;
  background:cornflowerblue;
}
@media screen and (max-width: 420px){
  .fixed{
    top:75%;
  }
  .pageTitle{
    font-size: 1.5em;
  }
}